.App {
  padding: 5px 60px;
  max-width: 100%;
  color: rgb(37, 37, 37);
}

.main-menu:hover,nav.main-menu.expanded {
  width: 140px;
  overflow:visible;
  background-color: #739e74ac;
}

.sidebarContainer {
  position: fixed;
  width: 50px;
  top: 0;
  left: 0;
  bottom: 0;
}

.main-menu {
  background:  rgba(18, 106, 25, 0.354);
  border-right:1px solid #acd0b2;
  position:relative;
  height:100%;
  overflow:hidden;
  -webkit-transition:width .5s linear;
  transition:width .05s linear;
  -webkit-transform:translateZ(0) scale(1,1);
  z-index:100;
}

.main-menu>ul {
  margin:5px -52px;
}

.main-menu li {
  position:relative;
  display:block;
  padding: 10px 0px
}

.main-menu li>a {
  position:relative;
  display:table;
  border-collapse:collapse;
  border-spacing:0;
  color:#212121;
  font-family: arial;
  font-size: 14px;
  text-decoration:none;
  -webkit-transform:translateZ(0) scale(1,1);
  -webkit-transition:all .1s linear;
  transition:all .1s linear;
}

.icon {
  position:relative;
  height:35px;
  text-align:center;
  vertical-align:middle;
}

#logo {
  height:38px;
  transform: translate(-13px, 7px);
}

.main-menu .nav-text {
  position:relative;
  display:table-cell;
  vertical-align:middle;
  width: 77px;
  font-size: 16px;
  font-family: 'Titillium Web', sans-serif;
}

.main-menu li:hover>a,.dropdown-menu>.active>a:focus {
  color:#000000;
  background-size: auto;
  background-color:#ffffffbb;
}

@font-face {
  font-family: 'Titillium Web';
  font-style: normal;
  font-weight: 300;
  src: local('Titillium WebLight'), local('TitilliumWeb-Light'), url(http://themes.googleusercontent.com/static/fonts/titilliumweb/v2/anMUvcNT0H1YN4FII8wpr24bNCNEoFTpS2BTjF6FB5E.woff) format('woff');
}

#headname {
  color: rgb(63, 63, 63);
  font-size: 30px;
}

#tsparticles {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: -1;
}

.App-header {
  display: flex
}

#logo {
  height: 40px;
}

#top {
  width: 70px;
  margin: 5px 5px;
  background-color: rgba(18, 106, 25, 0.354);
  border-radius: 12px;
  height: 40px;
  border: none;
}

#top:hover {
  box-shadow: 0 1px grey;
  background-color:  #81b18278;
}

#headname {
  margin-left: 5px;
}

a:visited {
  text-decoration: none;
  color: rgba(53, 53, 53, 0.279)
}