.AppOthers{
    padding: 5px 60px;
    max-width: 100%;
    color: rgb(37, 37, 37);
}

#pre {
    font-size: 16px;
}

#preface {
    border: 1px solid rgba(76, 135, 92, 0.539);
    text-align: center;
    color: rgb(67, 67, 67);
    border-radius: 12px;
    margin-left: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
}

#preface:hover {
    box-shadow: 1px 1px rgba(76, 135, 92, 0.539);
    color: rgb(76, 76, 76);
}

#formContainer {
    margin-left: 250px;
    float: left;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 600px;
}

input[type=text] {
    padding: 5px 20px;
    margin: 10px 0;
    box-sizing: border-box;
    border: 1.5px solid rgb(184, 150, 150);
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.733);
}

input[type=text]:hover {
    background-color: rgba(255, 255, 255, 0.468);
}

form {
    margin-left: 200px;
}

table {
    text-align: center;
    justify-content: center;
    align-items: center;
}

#submit {
    margin-right: 190px;
}

#displayContainer {
    margin-top: 35px;
    margin-bottom: 40px;
    margin-left: 20px;
    float: left;
    justify-content: center;
    align-items: center;
    text-align: center;
}

#artist, #track {
    width: 200px;
    word-wrap: break-word;
}

#reccoHeader {
    margin-left: 70px;
}

#mainLine {
    margin-left: 25px;
}

td {
    padding: 5px 90px;
    width: 100%;
}

#submitBtn {
    border: none;
    background-color: rgba(18, 106, 25, 0.354);
    color: rgb(255, 255, 255);
    border-radius: 12px;
    height: 25px;
    width: 90px;
    margin-top: 15px;
}

#submitBtn:hover {
    background-color: rgba(63, 113, 68, 0.562);
}

#artistLabel, #trackLabel {
    padding: 10px 10px
}

