.AppArtist {
    display: flex;
    color: rgb(67, 67, 67);
    margin-left: 70px
}

#display {
    display: flex;
    flex-wrap: wrap;
    margin-top: 15px
}

.artist {
    padding: 15px 25px;
}

.nav {
    background-color: rgba(50, 64, 77, 0);
    border: none
}

img {
    height: 150px;
    padding: 5px 20px;
    border-radius: 20%;
    opacity: 0.95;
}

img:hover {
    transform: scale(1.05);
    opacity: 1;
}

#name {
    font-size: 16px;
    width: 100px;
    margin-left: 50px;
    text-align: center;
}


