.main {
    color: rgb(59, 59, 59);
    margin-left: 100px;
    margin-top: 20px;
}

#header {
    text-align: center;
}

h2 {
    color: rgb(66, 60, 60);
    font-family: 'Courier New', Courier, monospace;
    font-size: 28px;
    width: auto;
    display: inline-block;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid transparent; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em; /* Adjust as needed */
    animation:
      typing 3.5s steps(40, end),
      blink-caret .75s 4.1;
}

/* The typing effect */
@keyframes typing {
    from { max-width: 0 }
    to { max-width: 100% }
  }

  /* The typewriter cursor effect */
  @keyframes blink-caret {
    0,
    100% {
      border-color: transparent
    }
    50% {
      border-color: #7D6F6F
    }
}

#header {
    border-bottom: 1px solid rgb(69, 51, 11);
}

p {
    font-size: 20px;
}

#tracksBox {
    display: flex;
    justify-content: center;
}

iframe {
    margin: 12px;
    opacity: 0.96;
}

#alb {
    height: 90px;
    position: relative;
    transition: height 0.3s;
    -webkit-transition: height 0.3s;
    text-align: center;
    overflow: hidden;
}

#alb:hover {
    height: 152px
}

#song {
    height: 90px;
    position: relative;
    transition: height 0.3s;
    -webkit-transition: height 0.3s;
    text-align: center;
    overflow: hidden;
}

#song:hover{
    height: 152px
}

iframe:hover {
    opacity: 1;
}